$filter-row: "filter-row";

$filter: "filter";
$filter-colum: "filter-colum";
$filter-primary: "filter-primary";
$filter-colum--large: "filter-colum--large";
$filter-search: "filter-search";
$filter-secondary: "filter-secondary";
$filter-header: "filter-header";
$filter-form: "filter-form";

.#{$filter-row}{}






.#{$filter-primary}{

    @media(min-width: theme('breakpoints.xl','value')){
    .#{$filter-colum}{ 
        flex:1;
        &--large{
            flex:3;
        }
            
        }
    }  
    @media(max-width: theme('breakpoints.xl','value')){
        .#{$filter-colum}{ 
            flex:1;
            &--large{
                flex:2;
            }
        }
    }  

    .#{$filter-row}{
    
        @media(max-width: theme('breakpoints.md','value')){
            display: flex; flex-direction: column;

            .#{$filter-colum}{ 
                &{order: 2;}
                &--large{order: 1; margin-bottom: theme('margin.6'); 
                    .#{$filter}{border-bottom: none; }
                }
               
            }

        }

        @media(min-width: theme('breakpoints.md','value')){
            &{display: flex; align-items: center;flex-wrap: wrap;}
        }

    }

    .#{$filter}{
        
    }

 }


.#{$filter-colum--large}{
    @media(min-width: theme('breakpoints.xl','value')){
        .filter__trigger--outlined{
            padding-#{theme('direction.right','value')}: theme('padding.16');
        }
    }
}


.#{$filter-secondary}{ 
    padding-top: theme('padding.2');
    .#{$filter-row}{
    
        @media(min-width: theme('breakpoints.xl','value')){
            &{display: flex; align-items: center;flex-wrap: wrap;}
        }
    }
    .#{$filter}{
        @media(max-width: theme('breakpoints.xl','value')){
            &__target{
                position: relative; padding: theme('padding.6'); box-shadow: none; border: none; border-bottom: 1px solid theme('color.gray.300');
        
            }
        }

        &__trigger{
            @media(min-width: theme('breakpoints.xl','value')){
                margin-#{theme('direction.right','value')}: theme('margin.5');
            }

            
        }
        &__trigger-label{
            @media(min-width: theme('breakpoints.xl','value')){
                font-weight: normal;
            }
        }
    }

    @media(min-width: theme('breakpoints.xl','value')){
        .#{$filter-colum}{ 
            &:first-child{
                .#{$filter}{
                    &__trigger{
                        padding-#{theme('direction.left','value')}: 0;
                    }
                }
            }        
        }
    }
}


.#{$filter-search}{ 

   width: 100%;
   @media(max-width: theme('breakpoints.md','value')){
        border: 1px solid theme('color.gray.300');
   }

        input{
            @media(max-width: theme('breakpoints.md','value')){
                padding-#{theme('direction.left','value')}: theme('padding.4') !important; font-weight: normal!important;
            }
        }

    input{
        border: none;
        padding: 0;
        color: inherit;
        padding:0 theme('padding.6');
        &:focus{
            box-shadow: none;
        }
        &::placeholder{
            color: inherit;
        }
        
    }

    .icon{
        width: 20px;
        height: 20px;
        opacity: 1;
        position: absolute; display: block!important;
        #{theme('direction.right','value')}: 13px; top: 50%; transform: translateY(-50%);

        
    }

}


.#{$filter}{
    width: 100%; position: relative;

    @media(max-width: theme('breakpoints.md','value')){
        &{ border-bottom: 1px solid theme('color.gray.300'); }
    }

    &__trigger{
            position: relative; height: 44px; line-height: 44px; padding:0 theme('padding.4'); cursor: pointer;text-align: #{theme('direction.left','value')};

        //     @media(max-width: theme('breakpoints.xl','value')){
        //         &{ border-bottom: 1px solid theme('color.gray.300');}
        // }
           
            @media(min-width: theme('breakpoints.md','value')){
                display: flex;
                align-items: center;
            }

            @media(max-width: theme('breakpoints.md','value')){
               font-weight: theme('font.weight.medium'); font-size: 14px;
            }
           
            .icon{
               
                @media(max-width: theme('breakpoints.md','value')){
                    &{display: none;}
                }

                margin-#{theme('direction.right','value')}: theme('margin.2');
                opacity: 0.5;
                min-width: 24px;
            }


        input{
            border: none;
            padding: 0;
            color: inherit; cursor: pointer;
       
            &:focus{
                box-shadow: none;
            }
            &::placeholder{
                color: inherit;
            }
            @media(max-width: theme('breakpoints.md','value')){
                font-weight: theme('font.weight.medium'); font-size: 14px;
             }
            
        }
    }

    .is-active{
        .filter__trigger-label{
            color: theme('color.primary');
        }
    }


    &__trigger--arrow{
        &:before{
            content: "";
            position: absolute;
            height: 8px;
            width: 8px;
            background: transparent;
            border-right: 2px solid theme('color.black');
            border-bottom: 2px solid theme('color.black');
            transform: translateY(-60%) rotate(45deg);
            #{theme('direction.right','value')}: 15px;
            
            @media(min-width: theme('breakpoints.xl','value')){
                #{theme('direction.right','value')}: 0;
            }

            top: 50%;
            opacity: 0.3; pointer-events: none;
            transition: all 0.3s; 
        }
    }

    &__trigger--outlined{
        
        @media(min-width: theme('breakpoints.md','value')){
            display: flex; align-items: center;  background-color:theme('color.white'); 
           padding-#{theme('direction.left','value')}:theme('padding.4'); padding-#{theme('direction.right','value')}:theme('padding.8'); outline:1px solid #ddd;
            &:before{
                #{theme('direction.right','value')}: 15px;
            }
        }

        @media(max-width: theme('breakpoints.xl','value')){
            &{font-size: theme('font.size.sm'); background-color: theme('color.white');}
        }
        @media(max-width: theme('breakpoints.md','value')){
            &{font-size:14px;}
        }
        
   
    }

    &__trigger--large{
        
        @media(min-width: theme('breakpoints.md','value')){
            height: 60px; line-height: 60px;
           
        }

   
    }

    &__target{
        position: relative; padding: theme('padding.4'); background-color: theme('color.white');
       

        @media(max-width: theme('breakpoints.md','value')){
            border-bottom: 1px solid theme('color.gray.300');
        }
        @media(min-width: theme('breakpoints.md','value')){
            position: absolute;
            border: 1px solid theme('color.gray.300'); z-index: 1; box-shadow: 0 5px 7px rgba(0,0,0,0.08); min-width: 260px;
            top: 100%;
            #{theme('direction.left','value')}: 0px;
        }


    }

    & .is-active{
        color: theme('color.primary');
        font-weight: 600;
        &:before{
            border-color: currentColor;
            opacity: 1;
            transform: translateY(-16%) rotate(-135deg);
        }
        .icon{
            opacity: 1;
        }
    }

}

.group-class-search{
    @media(max-width: theme('breakpoints.md','value')){
         &{padding: theme('padding.6') 0 theme('padding.4');}
    }

    .#{$filter}{
        &__target{ position: absolute; width: 100%;
           
        }

        @media(max-width: theme('breakpoints.md','value')){
            &{border: 1px solid theme('color.gray.300');
                &__trigger{
                    input{padding: 0!important;}
                }
            }
        }

    }
    
        .#{$filter-search}{
            @media(max-width: theme('breakpoints.md','value')){
                border: none;
            }

    }


    @media(max-width: theme('breakpoints.md','value')){
       
        .#{$filter}{
            &__trigger{ border-bottom: 1px solid theme('color.gray.300');}
            &__trigger--arrow{ border-bottom: none;}
        }

        
       
    }


}


.#{$filter-header}{ display: none;
    @media(max-width: theme('breakpoints.md','value')){
        &{display: inline-block;width: 100%; margin-bottom: theme('margin.4');padding:theme('padding.6');}
        }
    h3{
        text-align: #{theme('direction.left','value')};
    }
}



.#{$filter-form}{
    @media(max-width: theme('breakpoints.md','value')){
        bottom: 0;
        background: #fff;
        padding: 0;
        box-shadow: 0 0 10px rgba(0,0,0,0.38);
        #{theme('direction.left','value')}: 0; #{theme('direction.right','value')}: 0;
        margin: 0;
        overflow: auto;
        position: fixed;
        text-align: left;
        top: 0;
        opacity: 0;
        visibility: hidden;
        z-index: 999; 

       
    }
}


.show-filters-js {
    @media(max-width: theme('breakpoints.md','value')){
        &{position: fixed; left: 0; right: 0; bottom: 0; top: 0;}
    }
    .#{$filter-form}{
        @media(max-width: theme('breakpoints.md','value')){
            opacity: 1;
           
            visibility: visible;
        }
    }
}



$filter-secondary: "filter-secondary";

.#{$filter-secondary}{
    @media(max-width: theme('breakpoints.xl','value')){
        bottom: 0;
        background: #fff;
        padding: 0;
        box-shadow: 0 0 10px rgba(0,0,0,0.38);
        #{theme('direction.left','value')}: 0;
        margin: 0;
        overflow: auto;
        position: fixed;
        text-align: left;
        top: 0;
        opacity: 0;
        visibility: hidden;
        z-index: 999;
        width: 280px;
        transition: all 300ms ease 0s;
        transform: translate3d(-100%, 0px, 0px);
    }

    @media(max-width: theme('breakpoints.md','value')){
       
        position: static; box-shadow: none; width: 100%; transform: translate(0);transition:none;
       
    }
    @media(max-width: theme('breakpoints.xl','value')){
       
        .#{$filter}{
            &__trigger{ border-bottom: 1px solid theme('color.gray.300');}
        }
       
    }
    @media(max-width: theme('breakpoints.md','value')){
       
        .#{$filter}{
            &__trigger{ border-bottom: none;
            
           

            }
        }

        
       
    }
}

.show-filters-js {
    .#{$filter-secondary}{
        @media(max-width: theme('breakpoints.xl','value')){
            opacity: 1;
            transform: translate3d(0px, 0px, 0px);
            visibility: visible;
        }
    }
}


