}

.filter-form__inner{
    position: relative;
    @media(max-width: theme('breakpoints.md','value')){
        width: 100%;
    }
    @media(min-width: theme('breakpoints.md','value')) and (max-width: theme('breakpoints.xl','value')){
        width: 21%;
    }

    @media(min-width: theme('breakpoints.xl','value')) and (max-width: theme('breakpoints.xxxl','value')){
        width: 20%;
    }
    .filter__head{
        min-width: 220px;
        max-width: 220px;
        height: 60px;
        background-color: #fff;
        border: 1px solid theme('color.gray.200');
        display: flex;
        align-items: center;
        padding: 0 theme('padding.5');
        position: relative;
        padding-#{theme('direction.right','value')}: theme('padding.10');
        border-#{theme('direction.right','value')}: transparent;
        cursor: pointer;

        @media(max-width: theme('breakpoints.xxxl','value')){
            min-width: unset;
            max-width: unset;
            padding: 0 theme('padding.3');
            padding-#{theme('direction.right','value')}: theme('padding.12');
            &::before{
                #{theme('direction.right','value')}: 13px;
            }
        }

        @media(max-width: theme('breakpoints.md','value')){
            min-width: 100%;
            border-#{theme('direction.left','value')}: none;
            border-bottom: none;
            height: 47px;
            padding: 0 theme('padding.6');
            color: theme('color.dark');
            &:last-child{
                border-bottom: 1px solid theme('color.gray.200');
            }
            &::before{
                top: 18px !important;
            }
        }

        

        &::before{
            content: "";
            position: absolute;
            height: 8px;
            width: 8px;
            background: transparent;
            border-right: 2px solid theme('color.gray.500');
            border-bottom: 2px solid theme('color.gray.500');
            transform: translateY(-50%) rotate(45deg);
            #{theme('direction.right','value')}: 24px;
            top: 50%;
            transition: all 0.3s;
        }
        input{
            border: none;
            padding: 0;
            color: inherit;
            margin-bottom: 2px;
            &:focus{
                box-shadow: none;
            }
            &::placeholder{
                color: inherit;
            }
            
        }
        .icon{
            min-width: 24px;
            height: 24px;
            margin-#{theme('direction.right','value')}: theme('margin.2');
            fill: theme('color.dark');
            opacity: 0.5;
        }
        h6{
            font-weight: normal;
            font-size: 14px;
            color: inherit;
        }

    }

    .filter__body {
        position: absolute;
        top: 100%;
        left: 0px;
        background: #fff;
        box-shadow: 0 5px 7px rgba(0,0,0,0.08);
        z-index: 1;
        border: 1px solid theme('color.gray.200');
        @media(max-width: theme('breakpoints.md','value')){
            width: 100%;
            position: static;
            border-#{theme('direction.left','value')}: none;
            border-#{theme('direction.right','value')}: none;
        }
    }

}

.filter--search{
    @media(min-width: theme('breakpoints.md','value')) and (max-width: theme('breakpoints.xl','value')){
        width: 37%;
        
        .icon{
            #{theme('direction.right','value')}: 6px !important;
        }
    }

    @media(min-width: theme('breakpoints.xl','value')) and (max-width: theme('breakpoints.xxxl','value')){
        width: 40%;
    }
    .filter__head{
        position: relative;
        border: 1px solid theme('color.gray.200');
        @media(min-width: theme('breakpoints.md','value')){
            padding-#{theme('direction.right','value')}: theme('padding.18');
        }

        @media(max-width: theme('breakpoints.md','value')){
            padding-#{theme('direction.left','value')}: theme('padding.14');
            padding-#{theme('direction.right','value')}: theme('padding.8');
        }

        @media(min-width: theme('breakpoints.xxxl','value')){
            max-width: 530px;
            min-width: 530px;
            width: 100%;
        }
    
        @media(min-width: theme('breakpoints.md','value')) and (max-width: theme('breakpoints.xl','value')){
            padding-#{theme('direction.right','value')}: theme('padding.12');
        }
    
        &::before{
            content: none;
        }
        .icon{
            width: 20px;
            height: 20px;
            opacity: 1;
            position: absolute;
            #{theme('direction.right','value')}: 13px;
            @media(max-width: theme('breakpoints.md','value')){
                #{theme('direction.left','value')}: 13px;
                #{theme('direction.right','value')}: unset;
            }
        }
        input{
            &::placeholder{
                color: theme('color.gray.700');
            }
        }
    }
}

.filter-form{

