.#{$offers-ui}{
    &{position: relative; display: inline-flex; align-items: center;
        &__trigger{display: flex; align-items: center; color: theme('color.third'); font-weight: theme('font.weight.medium');text-decoration:underline; text-decoration-style: dashed; text-underline-offset: 3px; cursor: pointer; padding: theme('padding.2');
            @media(max-width: theme('breakpoints.xl','value')){
                &.is-active:after{position: fixed; left: 0;right: 0; top: 0; bottom: 0; background-color: rgba(0,0,0,0.8); content: "";z-index: 100;}
            }
    
        
        }
       

        &__target{position: absolute; #{theme('direction.left','value')}: 0;top: 100%; background-color: #fff; box-shadow: 0 20px 30px 0 rgba(0,0,0,.08); z-index: 2; width: 100%; border:1px solid theme('color.gray.200');  min-width: 24rem; padding: theme('padding.6'); display: none;

     

        @media(max-width: theme('breakpoints.xl','value')){
            &{position: fixed; left:50%; top: 50%; transform: translate(-50%, -50%); z-index: 1000; width: auto;
                &:before{position: absolute; right: 0;top: -50px; width: 40px; height: 40px; content: "+"; transform: rotate(45deg); color: #fff; font-size: 2.4rem; text-align: center; pointer-events: none;}
            }
        }

            &-body{max-height: 190px; overflow: auto;
                @media(max-width: theme('breakpoints.xl','value')){
                    &{max-height: 450px;}
                }
            
            }
        }
    }
}

